/*#gridMain table td,*/
#gridMain{font-size: 16px !important;}
#gridMain table th{font-size: 16px !important;}
/*#gridMain { display: flex;  flex-wrap: nowrap; width: 80vw  }*/
/*#gridMain > div { height: 80vh;}*/
.frame_cnt{height: 100%; width: 100%;}
#gridMain{ width: 80vw;}
#mapDiv{ width: 100%; padding: 5px; border: black 1px dashed;}
#menuDiv{ width: 100%; min-width: 624px; }

#mapCont { width: 100%; height: 40vh; }
#mapCont {  }
.mapTile { border: #000170 solid 1px}


@media only screen and (min-width: 1305px) {

    #gridMain{position: relative}

    #mapDiv {
        width: 39%;
        float: left;
    }
    #menuDiv {
        width: 59%;
        float: right;
    }
    #mapCont{
        height: 80vh;
    }
}


#RightDiv > div > div { height: 30vh !important; }

#moveBtnDiv button,
#saveBtnDiv button{ margin: 5px; }
#moveBtnDiv button img,
#saveBtnDiv button img{ width: 20px; height: 20px; padding: 5px; }

#infoButton { -webkit-align-self: end; align-self: end; border: black 1px dashed !important; margin: 5px }
#infoButton span{ font-size: medium; font-size: initial; padding: 5px; padding-right: 0 }
#infoButton img{ width: 20px; height: 20px; padding: 5px }

#tankDiv img { width: 100px; height: 100px; }

#commandsDiv { height: 20vw; min-width: 315px}

#commands th{ position: -webkit-sticky; position: sticky; top: 0; height: 5px; font-size: 10px !important;}
#commands td,
#commands th{ text-align: center; background: lightgray; border:  2px solid black; }
#commands tr > td:nth-child(1){ width: 20px}

.selectedRow {
    border: 4px  solid  #F00 ;
}
.currentRow{
    border: 4px solid  #0A0 ;
}
.selectedRow.currentRow{
    border-left: 4px solid  #F00 !important;
    border-right: 4px solid  #F00 !important;
}

/*#gameButtonsDiv{ min-width: 232px; }*/

.gameButtonsColumn { min-width: 60px; }


.gameTD{
    margin: 2px;
    width: 50px;
    height: 50px;
}

#gameButtons { display: -webkit-flex; display: flex; -webkit-flex-wrap: wrap; flex-wrap: wrap; -webkit-justify-content:space-evenly; justify-content:space-evenly }

.driver.selected{ border: #C00 solid !important; }
.gunner.selected{ border: #0C0 solid !important; }
.charger.selected{ border: #00C solid !important; }

.flex-column{ display: -webkit-flex; display: flex; -webkit-flex-direction: column; flex-direction: column; -webkit-align-items: center; align-items: center}
.flex-row{ display: -webkit-flex; display: flex; -webkit-flex-direction: row; flex-direction: row; -webkit-align-items: center; align-items: center}
.flex-justify-start {-webkit-justify-content: flex-start;justify-content: flex-start;}
.flex-justify-center {-webkit-justify-content: center;justify-content: center;}
.flex-justify-evenly {-webkit-justify-content:space-evenly;justify-content:space-evenly;}
.flex-justify-between {-webkit-justify-content:space-between;justify-content:space-between;}
.flex-align-start {-webkit-align-items: flex-start;align-items: flex-start;}
.flex-align-end {-webkit-align-items: flex-end;align-items: flex-end;}
.flex-wrap {-webkit-flex-wrap: wrap;flex-wrap: wrap}

.padding-1 {padding: 1px;}
.padding-2 {padding: 2px;}
.padding-5 {padding: 5px;}
.padding-10 {padding: 10px;}
.margin-1 {margin: 1px;}
.margin-2 {margin: 2px;}
.margin-5 {margin: 5px;}
.margin-10 {margin: 10px;}

.widthAll { width: 100%; }
.overflow { overflow: auto}
.overflow-visible { overflow: scroll}
.overflow-x-visible { overflow-x: scroll}
.overflow-y-visible { overflow-y: scroll}
.overflow,
.overflow-visible{
    scrollbar-color: #777777 #F5F5F5;
    scrollbar-width: thin;
}

.overflow::-webkit-scrollbar-track,
.overflow-visible::-webkit-scrollbar-track
{
    border: 1px solid black;
    background-color: #F5F5F5;
}

.overflow::-webkit-scrollbar,
.overflow-visible::-webkit-scrollbar
{
    width: 10px;
    background-color: #F5F5F5;
}

.overflow::-webkit-scrollbar-thumb,
.overflow-visible::-webkit-scrollbar-thumb
{
    background-color: #000000;
}

.ReactModal__Body--open,
.ReactModal__Html--open{
    overflow: hidden;
}

.ReactModal__Overlay{
    z-index: 99999;
}
